/* navbar import*/
.nav-container {
    display: flex;
    justify-content: flex-start;
    font-family: 'CustomFont', sans-serif;
    border-radius: 10px;
    border: solid #0a481e;
    margin-right: 56%;
    


    & main {
      display: flex;
      flex-direction: row;
      font-family: 'CustomFont', sans-serif;
      border-radius: 10px;
      font-size: 20px;
      color: white;
      align-items: center;
      
      & h3 {
        border-radius: 10px;
        padding: 20px;
        border: 2px outset;
        margin: 2%;
        

        &:hover {
          border-radius: 10px;
          background-color: #800020;
          border-style: inset;
        }
      }
    }
  
  }